import React from 'react'
import file1 from './shared/images/bbx2.jpg';
import file2 from './shared/images/natak.jpg';
const aboutimg = () => {
    return (  
        <React.Fragment>
            <div className="col-6">
            <div className="card bg-darken-1 mb-3" style={{width:500}}>
  <img className="card-img-top" src={file1} style={{width:"1000", height:600}} alt="Card image cap"/>
  <div className="card-body">
    <h5 className="card-title">I AM A BEATBOXER</h5>
    <p className="card-text">This is a National Youth Festival Participation in Beatboxing in MMU(Maharishi Markandeshwar University)<br/>
   <br/><span className="badge badge-danger"> I am The First National Winner across North Zone</span></p>
    <p className="card-text"><small class="text-muted">Last updated 3 mins ago</small>
    <br/>
    <br/><br/></p>
  </div>
</div>
</div>
<div className="col-6">
<div className="card bg-darken-1 mb-3" style={{width:500}}>
  <img className="card-img-top" src={file2} style={{width:"1000", height:600}} alt="Card image cap"/>
  <div className="card-body">
    <h5 className="card-title">I AM A THEATRE LOVER</h5>
    <p className="card-text">This moment is that when i represented my university at AIU(Association of Indian University).<br/>
    I am only one who is participating in every category of theatrical events.<br/>
    <span className="badge badge-danger">Lpu got overall THIRD in Theatrical Event</span> I was lucky soo much to a part of That Event.</p>
    <p className="card-text"><small class="text-muted">Last updated 3 mins ago</small></p>
  </div>
</div>
</div>
            </React.Fragment>
    );
}
export default aboutimg;