import React from 'react';
import {NavLink} from 'react-router-dom';
const navbar = () => {
    return (
    <div>
        <nav className="navbar navbar-expand-lg navbar-light" style={{padding:30}}>
  <NavLink to="/" className="navbar-brand mb-0 h1"  style={{fontSize:50}}>
   HK
  </NavLink>
   <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
    </button> 
  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-5 nav-fill">
      <li className="nav-item active">
        <NavLink to="/" className="nav-link" >Home <span className="sr-only">(current)</span></NavLink>
      </li>
      <li className="nav-item">
        <NavLink to="/contact" className="nav-link">Contact Me</NavLink>
      </li>
      
      <li className="nav-item">
        <NavLink to="/about" className="nav-link">About Me</NavLink>
      </li>

      <li className="nav-item">
        <NavLink  to ="/resume" className="nav-link"  tabindex="-1" aria-disabled="false">Resume</NavLink>
      </li>
    </ul>
    </div>
   </nav>
      </div>
      );
}
 
export default navbar;