import React from 'react';
import AbImg from './aboutimage';
const aboutme = () => {
    return (  
        <React.Fragment>
         <div className="container">
                    <div className="row">
                        <div className="col text-center">
                <h1><span className="badge badge-primary" style={{fontSize:50,color:"black"}}>About Me</span>
                </h1>  
                </div>           
                </div>
                <div className="row mt-5">
                    <div className="col text-center">
                         <h1 style={{fontSize:30}}>Find out who I am, where I'm from and what I'm all about. <br/></h1>
                    </div>
                </div>
                <div className="row">
                    <div className="col text-center">
                        <hr style={{width:250,borderTop:"1px solid black",height:30}}/>
                <div class="jumbotron">
                <h1 style={{fontSize:50}}>Who I Am</h1>
                  {/* <h1 className="badge badge-primary" style={{fontSize:60,color:"black"}} >Text</h1> */}
                  <hr style={{width:150,borderTop:"2px solid black",height:30}}/>
                  <p style={{fontSize:30}}>My name's Harsh Kaushik. I'm a web designer and developer.</p>       
                  <p style={{fontSize:20}}>During the day I’m the lead developer at a local agency. During the evenings I spend my time
                  working on freelance projects and building my own products</p> 
                   <br/>
                   <p style={{fontSize:30}}>Apart from developing or designing.<br/>I am a <span className="badge badge-danger">ARTIST</span> as Well.</p>
                   <hr style={{width:250,borderTop:"1px solid black",height:30}}/>
                    </div>
                    </div>
                    </div>
                    <div className="row">
                       <AbImg/>
                    </div>
                    <div className="row mt-5">
                        <div className="col">
                            <div className="jumbotron">
                            <h1><span style={{fontSize:50,color:"black"}}>Thanks for showing Interest In Me!!!!</span></h1>
                                </div>
                        </div>
                </div>
                </div>
    
            </React.Fragment>
    );
}
 
export default aboutme;