import React,{Component} from 'react';
import Contact from './contactchild';
class contact extends Component {
    state = {  }
    render() { 
        return (  
            <React.Fragment>
                <div className="container">
                    <div className="row">
                        <div className="col text-center">
                <h1><span className="badge badge-primary" style={{fontSize:50,color:"black"}}>Contact Me</span>
                </h1>  
                </div>           
                </div>
                <div className="row mt-5">
                    <div className="col text-center">
                         <h1 style={{fontSize:30}}>If you’d like to chat about a project please fill in the form <br/>below and I’ll get back within 1-2 days.</h1>
                    </div>
                </div>
                <div className="row">
                    <div className="col text-center">
                        <hr style={{width:250,borderTop:"1px solid black",height:30}}/>
                <div class="jumbotron">
                <h1 style={{fontSize:50}}>Status</h1>
                  <h1 className="badge badge-primary" style={{fontSize:60,color:"black"}} >Available</h1>
                  <hr style={{width:150,borderTop:"2px solid black",height:30}}/>
                  <p style={{fontSize:30}}>I’m currently taking on new projects!</p> 
                  <div>
                      <h1>
                          Form Is Here
                          </h1>
                          <Contact/>
                      </div>
                    </div>
                    </div>
                    </div>
                </div>
                </React.Fragment>
        );
    }
}
 
export default contact;