import React,{Component} from'react';
import Input from './inputcontact';
class contactchild extends Component {
    state = { 
        account: {Name: "",Email: "",Textarea: "",},
        errors: {},
     }
     componentDidMount()
     {
        console.clear();
     }

     validate = () =>
     {
       const errors={};
       const {account}=this.state;
       if(account.Name.trim()===''){
         errors.Name="Name is Required";
       }
       if(account.Email.trim()==='')
       {
         errors.Email="Email is Required";
       }
       if(account.Textarea.trim()===''){
        errors.Textarea="Please Write Something";
      }
        return Object.keys(errors).length === 0 ? null: errors;
     }

    handleSubmit = (event) => {
           event.preventDefault();
          const errors = this.validate();

          this.setState({ errors: errors || {} });
              if (errors) return;
                console.log("Submitted");

    }

    validateProperty=({name,value})=>
    {
       if(name=='Name')
       {
         if(value.trim()==='')return "Name is Required";
       }
       if(name=='Email')
       {
         if(value.trim()==='')return "Email is Required";
       }
       if(name=='Textarea')
       {
         if(value.trim()==='')return "Text is Required";
       }
    }
    handleChange = ({currentTarget: input}) =>
    {
             const errors = { ...this.state.errors };
             const errorMessage = this.validateProperty(input);
            if (errorMessage) errors[input.name] = errorMessage;
              else delete errors[input.name];

                const account = {...this.state.account};
                account[input.name]= input.value;
                this.setState({account,errors:errors || {}});
    }
    render() { 
           const { account } = this.state;
        return ( 
            <React.Fragment>
                <div className="container mt-5">
          <form onSubmit={this.handleSubmit}>
          <div class="row">
            <div class="col-4">
            <Input
              id="Name"
              name={"Name"}
              label="Name"
              type="text"
              value={account.Name}
              onChange={this.handleChange}
              error={this.state.errors.Name}
            />
            </div>
            <div class="col-8">
            <Input
              id="Email"
              name={"Email"}
              label="Email"
              type="Email"
              value={account.Email}
              onChange={this.handleChange}
             error={this.state.errors.Email}
            />
            </div>
            </div>
             
             <div class="row">
               <div class="col">
             <label htmlfor="Textarea">How Can I Help?</label>
    <textarea className="form-control text-centre" id="Textarea" name={"Textarea"} type="Textarea" rows="3" value={account.Textarea}
              onChange={this.handleChange}
              error={this.state.errors.Textarea}></textarea>
               </div>
              </div>
            <button className="btn btn-outline-secondary btn-lg mt-4" type="submit">
              Get In Touch
            </button>

          </form>
        </div>
                </React.Fragment>
         );
    }
}
 
export default contactchild;

