import React from 'react';
import file from './shared/pdffile/resume.pdf';
const resume = () => {
    return (  
        <React.Fragment>
          
          <div className= "container">
              <div className="row">
              <div className="col text-center">
                  <div className="jumbotron">
                <h1><span className="badge badge-primary" style={{fontSize:50,color:"black"}}>Resume Is Here</span>
                </h1>  

        <div className = "App" >
        <a href = {file} target = "_blank" className="fa fa-download badge  badge-danger mt-5" > Download Pdf</a>
         
      </div>
      </div>
                </div>  
                  </div>
          </div>
            </React.Fragment>
    );
}
 
export default resume;